<template>
    <div>

    <el-form label-width="80px">
        <el-row :gutter="40">
            <el-col :span="8">
        <el-form-item>
        <el-switch
                v-model="value"
                active-text=""
                inactive-text=""
                @change="dealSwitch(value)"
        >
        </el-switch>
        </el-form-item>
            </el-col>
            <el-col :span="5" v-if="this.value">
                <el-button   type="primary" icon="el-icon-edit" @click="submit"></el-button>
            </el-col>
        </el-row>
    <el-row :gutter="20">
        <el-col :span="8">
        <el-form-item label="" >
            <el-input v-model="list.name" :readonly="!value"></el-input>
        </el-form-item>
        </el-col>
        <el-col :span="8">
        <el-form-item label="" >
            <el-input v-model="list.type" :readonly="!value"></el-input>
        </el-form-item>
        </el-col>
    </el-row>
        <el-row :gutter="20">
            <el-col :span="8">
                <el-form-item label="" >
                    <el-input v-model="list.address" :readonly="!value"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="8">
                <el-form-item label="" >
                    <el-input v-model="list.website" :readonly="!value"></el-input>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row :gutter="20">
            <el-col :span="8">
                <el-form-item label="" >
                    <el-input v-model="list.telephone" :readonly="!value"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="8">
                <el-form-item label="" >
                    <el-input value="" :readonly=true></el-input>
                </el-form-item>
            </el-col>
        </el-row>
        <el-form-item label="" >
            <el-input type="textarea" autosize v-model="list.introduction" ></el-input>
        </el-form-item>
        <el-form-item label="" v-if="!this.value">
        <el-image :src="list.headPicture" style="width: 250px;height: 250px" >
            <el-input v-model="list.headPicture" :readonly=true  v-if="this.value"></el-input>
        </el-image>
        </el-form-item>
        <el-col :span="12">
        <el-form-item label="" v-if="this.value">
            <el-upload
                    class="upload-demo"
                    ref="upload"
                    :http-request="upload"
                    :file-list="fileList"
                    :auto-upload="false"
            >
                <el-button slot="trigger" size="small" type="primary"></el-button>
                <div slot="tip" class="el-upload__tip"></div>
            </el-upload>
        </el-form-item>
            </el-col>
    </el-form>
    </div>
</template>

<script>
    export default {
        name: "merInfo",
        data(){
            return{
                list:{},
                value: false,
                fileList:[],
                id:1
            }
        },
        methods:{
            dealSwitch(value){
                if(value===false){
                    this.initPage()
                }
            },
            initPage(){
                this.axios.get(`/api/exhibition/company/getInformationByCompanyId?id=${this.id}`)
                    .then((res)=>{
                        this.list=res.data.data.companyInformation
                    })
            },
            submit(){
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'success'
                }).then(() => {
                    let filelist=this.$refs.upload.uploadFiles
                    let formData=new FormData
                    let headerConfig={headers:{'Content-Type': 'multipart/form-data'}};
                    if(filelist[0]){
                        formData.append('file',filelist[0].raw)
                    }
                        // ?companyId=1&name=${this.list.name}&address=${this.list.addresss}&website=${this.list.website}&type=${this.list.type}&tel=${this.list.telephone}&introduce=${this.list.introduction}
                    this.axios.post(`/api/exhibition/company/updateInformation?companyId=1&name=${this.list.name}&address=${this.list.addresss}&website=${this.list.website}&type=${this.list.type}&tel=${this.list.telephone}&introduce=${this.list.introduction}`,formData,headerConfig).then(()=>{
                        this.$message({
                            message: '',
                            type: 'success'
                        })
                    })
                .catch((error)=>{
                        this.$message({
                            message: '',
                            type: 'error'
                        })
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            }
        },
        created() {
            if(this.$route.query.id){
                this.id=this.$route.query.id
            }
            this.initPage()
        }
    }
</script>

<style scoped>

</style>
