<template>
    <div class="main">
        <el-page-header @back="goBack" content="" style="margin-bottom: 20px">
        </el-page-header>
        <el-form label-width="100px" class="demo-ruleForm" >
            <el-form-item label="" prop="name">
                <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>
            <el-form-item label="" >
                <el-col :span="11">
                    <el-form-item prop="date1">
                        <el-date-picker type="date" placeholder="" v-model="ruleForm.startTime" style="width: 100%;"></el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col class="line" :span="2">-</el-col>
                <el-col :span="11">
                    <el-form-item prop="date2">
                        <el-date-picker type="date" placeholder="" v-model="ruleForm.endTime" style="width: 100%;"></el-date-picker>
                    </el-form-item>
                </el-col>
            </el-form-item>
            <el-row :gutter="20">
                <el-col :span="7">
                    <el-form-item label="id" prop="id">
                        <el-input v-model="ruleForm.exhibitionHallId"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="7">
                    <el-form-item label="" prop="showRoom">
                        <el-input v-model="ruleForm.session"></el-input>
                    </el-form-item></el-col>
                <el-col :span="7">
                    <el-form-item label="" prop="acreage">
                        <el-input v-model="ruleForm.period"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
                    <el-form-item label="" prop="picture">
                        <el-upload
                                class="upload-demo"
                                ref="upload"
                                :http-request="upload"
                                :file-list="fileList"
                                :auto-upload="false"
                        >
                            <el-button slot="trigger" size="small" type="primary"></el-button>
                            <div slot="tip" class="el-upload__tip"></div>
                        </el-upload>
                    </el-form-item>
            <el-row :gutter="20">
                <el-col :span="14">
                    <el-form-item label="" prop="introduction">
                        <el-input v-model="ruleForm.introduction" type="textarea" :autosize="{ minRows: 3, maxRows: 4}"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="">
            <el-tag
                    :key="tag"
                    v-for="tag in dynamicTags"
                    closable
                    :disable-transitions="false"
                    @close="handleClose(tag)">
                {{tag}}
            </el-tag>
            <el-input
                    class="input-new-tag"
                    v-if="inputVisible"
                    v-model="inputValue"
                    ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm"
                    @blur="handleInputConfirm"
            >
            </el-input>
            <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submit()">{{buttonName}}</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "edit",
        data() {
            return {
                ruleForm: {
                    name: '',
                    startTime:"",
                    endTime:"",
                    exhibitionHallId:"",
                    session:'',
                    period:"",
                    introduction:'',
                    icon:''
                },
                rule:{
                    id:[]
                },
                buttonName:'',
                dynamicTags: [],
                inputVisible: false,
                inputValue: ''
            };
        },
        methods: {
            submit() {
                this.ruleForm.subAreaList=this.dynamicTags
                this.id=1
                this.ruleForm.startTime=new Date(this.ruleForm.startTime).Format('yyyy-MM-dd')
                this.ruleForm.endTime=new Date(this.ruleForm.endTime).Format('yyyy-MM-dd')
                this.axios.post(`/api/exhibition/organizer/holdExhibition?subAreaList=${this.dynamicTags}&userId=1&name=${this.ruleForm.name}&startTime=${this.ruleForm.startTime}&endTime=${this.ruleForm.endTime}&exhibitionHallId=${this.ruleForm.exhibitionHallId}&session=${this.ruleForm.session}&period=${this.ruleForm.period}&introduce=${this.ruleForm.introduction}&icon==${this.ruleForm.icon}`)
                .then((res)=>{
                    this.$message.success(',')
                    this.$router.push({path:'/mangeex'})
                })
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            goBack(){
                this.$router.back()
            },
            handleClose(tag) {
                this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
            },

            showInput() {
                this.inputVisible = true;
                this.$nextTick(_ => {
                    this.$refs.saveTagInput.$refs.input.focus();
                });
            },

            handleInputConfirm() {
                let inputValue = this.inputValue;
                if (inputValue) {
                    this.dynamicTags.push(inputValue);
                }
                this.inputVisible = false;
                this.inputValue = '';
            }
        },
        created(){
            if(this.$route.params.data){
                this.ruleForm=this.$route.params.data
                this.ruleForm.startTime=new Date(this.ruleForm.startTime)
                this.buttonName=''
            }

        }
    }
</script>

<style scoped>

</style>
