<template>
    <div id="main">
        <el-page-header @back="goBack" content="" style="margin-bottom: 20px">
        </el-page-header>
        <el-table
                :data="tableData"
                style="width: 100%">
            <el-table-column
                    prop="id"
                    label=""
                    width="180">
            </el-table-column>
            <el-table-column
                    label=""
                    width="180">
                <template slot-scope="scope">
                    <el-link type="primary" @click="handleEdit(scope.$index)">{{scope.row.name}}</el-link>
                </template>
            </el-table-column>
            <el-table-column
                    prop="owner"
                    label="">
            </el-table-column>
            <el-table-column
                    label="" >
                <template slot-scope="scope">
                    <el-button size="small" @click="handleEdit(scope.$index)"></el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    const axios = require('axios')
    export default {
        name: "exhiDetails",
        data(){
            return{
                tableData:[{
                    id: '1',
                    name: '',
                    address: ' 1518 ',
                }, {
                    id: '1',
                    name: '',
                    owner: ' 1517 ',
                }, {
                    id: '1',
                    name: '',
                    owner: ' 1519 ',
                }, {
                    id: '1',
                    date: '2016-05-03',
                    owner: '',
                }]
            }
        },
        methods:{
            handleEdit(scope){
                this.$router.push('/store')
            },
            goBack(){
                this.$router.back()
            }
        },
        created() {
            axios.get('http://localhost:3000/store').then(res=>this.tableData=res.data
            )
        }
    }
</script>

