<template>
    <div id="goods">
        <el-row :gutter="20" id="search_bar">
            <el-col :span="6"><el-input v-model="searching" placeholder="" style="margin-bottom: 20px;width: 300px"></el-input></el-col>
            <el-col :span="2"><el-button  @click="handleSearch"></el-button></el-col>
            <el-col :span="6"><el-button  @click="handleId">ID</el-button></el-col>
            <el-col :span="5"><el-button  @click="handleNew"></el-button></el-col>
            <el-col :span="2"><el-button  @click="handlePage('up')"></el-button></el-col>
            <el-col :span="2"><el-button  @click="handlePage('next')"></el-button></el-col>
        </el-row>
        <el-table
                :data="tableData"
                style="width: 100%">
            <el-table-column
                    prop="goodsId"
                    label="id"
                    width="180">
            </el-table-column>
            <el-table-column
                    label=""
                    width="150"
            >
                <template slot-scope="scope">
                    <div class="text">{{scope.row.goodsName}}</div>
                </template>
            </el-table-column>
            <el-table-column
                    prop="priority"
                    label="">
            </el-table-column>
            <el-table-column
                    prop="currentPrice"
                    label="">
            </el-table-column>
            <el-table-column
                    label="">
                <template slot-scope="scope">
                    {{type[scope.row.categoryId]}}
                </template>
            </el-table-column>
            <el-table-column
                    label="">
                <template slot-scope="scope">
                    <el-tag  >{{state[scope.row.goodsStatus]}}</el-tag>
                </template>
            </el-table-column>
            <el-table-column
                    label="" >
                <template slot-scope="scope">
                    <el-button size="small" @click="handleEdit(tableData[scope.$index])"></el-button>
                    <el-button size="small" @click="handleApply(scope.row.goodsId)"></el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: "goods",
        data() {
            return {
                tableData: [
                    {
                        goodsId:1,
                        goodsName:'',
                        originalPrice:'20',
                        currentPrice:'15',
                        originPlace:''
                    }
                ],
                type:[
                    '','','','',''
                ],
                state:['','','','','',''],
                id:7
            }
        },
        methods:{
            handleApply(id){
                this.$prompt('(0123)', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    inputPattern: /[0-3]/,
                    inputErrorMessage: '0123'
                }).then(({ value }) => {
                    this.axios.post(`/api/exhibition/goods/modify/priority?goodsId=${id}&priority=${value}`)
                    .then(()=>{
                            this.init()
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            handleEdit(item){
                this.$router.push({name:'addGoods',params:{data:item}})
            },
            handleNew(){
                this.$router.push({name:'addGoods'})
            },
            init(){
                this.axios.get(`/api/exhibition/goods/query/company?companyId=${this.id}&goodsStatus=0&pageNum=1&pageSize=10`).then(res=>this.tableData=res.data.data.list)
            }
        },
        created() {
            if(this.$route.query.id){
                this.id=this.$route.query.id
            }
            this.init()
        }
    }
</script>

<style scoped>
    .image {
        width: 100%;
        display: block;
    }
    .text{
        overflow:hidden;
        text-overflow:ellipsis;
        display:-webkit-box;
        -webkit-line-clamp:2;
        -webkit-box-orient:vertical;
    }
</style>
