<template>
    <div>
        <h3>/ </h3>
        <div style="display: flex">
            <div class="left">
                <el-select v-model="value" placeholder="" style="margin-bottom:15px">
                    <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                </el-select>
                <div style="width: 300px">
                    <el-upload
                            class="upload-demo"
                            drag
                            action="https://jsonplaceholder.typicode.com/posts/"
                            multiple
                            :auto-upload="false"
                    >
                        <i class="el-icon-upload"></i>
                        <div class="el-upload__text"><em></em></div>
                        <div class="el-upload__tip" slot="tip"></div>
                    </el-upload>
                    <el-button style="margin-top:10px;" size="small" type="success" @click="submitUpload"></el-button>
                </div>
            </div>
            <div class="right">
                <el-table
                        :data="tableData"
                        style="width: 100%">
                    <el-table-column
                            prop="date"
                            label=""
                            width="180">
                    </el-table-column>
                    <el-table-column
                            prop="name"
                            label=""
                            width="180">
                    </el-table-column>
                    <el-table-column
                            label="">
                        <template slot-scope="scope">
                            <el-tag :type="scope.row.status==1?'info':(scope.row.status==2?'success':'danger')">{{scope.row.status==1?'':(scope.row.status==2?'':'')}}</el-tag>
                        </template>
                    </el-table-column>
                    <el-table-column
                            label="" >
                        <template slot-scope="scope">
                            <el-button size="small" v-if="scope.row.status==1" type="danger" @click="handleEdit(scope.$index)"></el-button>
                            <el-button size="small" v-if="scope.row.status!==1"  @click="handleEdit(scope.$index)"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "applyGood",
        data(){
            return{
                tableData: [{
                    date: '2016-05-02',
                    name: '',
                    status: 1
                }, {
                    date: '2016-05-04',
                    name: '',
                    status: 1
                }, {
                    date: '2016-05-01',
                    name: '',
                    status: 3
                }, {
                    date: '2016-05-03',
                    name: '',
                    status: 2
                }],
                options: [{
                    value: '1',
                    label: ''
                }, {
                    value: '2',
                    label: ''
                }, {
                    value: '3',
                    label: ''
                }, {
                    value: '4',
                    label: ''
                }],
            }
        }
    }
</script>

<style scoped>
    .right{
        width: 600px;
        margin-left: 200px;
    }
</style>
