<template>
    <div>
        <el-table
                :data="tableData"
                style="width: 100%">
            <el-table-column
                    prop="date"
                    label=""
                    width="130">
                <template slot-scope="scope">
                <el-image
                        style="width: 100px; height: 100px"
                        :src="scope.row.headPicture"
                        fit="contain"></el-image>
            </template>
            </el-table-column>
            <el-table-column
                    prop="name"
                    label=""
                    width="200">
            </el-table-column>
            <el-table-column
                    prop="telephone"
                    label=""
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="introduction"
                    label=""
                    width="180">
                <template slot-scope="scope">
                    <div  class="text">
                        {{scope.row.introduction}}
                    </div>
                </template>
            </el-table-column>
            <el-table-column
                    label=""
                    width="180">
                <template >
                    <el-link type="primary"></el-link>
                </template>
            </el-table-column>
            <el-table-column
                    label="" >
                <template slot-scope="scope">
                    <el-button size="small" @click="handlePass(scope.row.id)"></el-button>
                    <el-button size="small" type="danger" @click="handleEdit(scope.$index)"></el-button>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
    export default {
        name: "auditGood",
        data(){
            return{
                tableData: [{
                    date: '2016-05-02',
                    name: '',
                    storename:'',
                    exhibition:'',
                    status: 1
                }, {
                    date: '2016-05-04',
                    name: '',
                    storename:'',
                    exhibition:'',
                    status: 1
                }, {
                    date: '2016-05-01',
                    name: '',
                    storename:'',
                    exhibition:'',
                    status: 3
                }, {
                    date: '2016-05-03',
                    name: '',
                    storename:'',
                    exhibition:'',
                    status: 2
                }]
            }
        },
        methods:{
            init(){
                this.axios.post(`/api/exhibition/organizer/checkCompanyApplyByExhibitionId?exhibitionId=2`).then((res)=>{
                    this.tableData=res.data.data
                })
            },
            handlePass(id){
                this.axios.post(`/api/exhibition/organizer/verifyCompanyApplyByExhibitionId?companyId=${id}&exhibitionId=2`).then((res)=>{
                    this.$message.success('')
                    this.init()
                })
            }
        },
        created() {
            this.init()
        }
    }
</script>

<style scoped>
    .text{
        overflow:hidden;
        text-overflow:ellipsis;
        display:-webkit-box;
        -webkit-line-clamp:3;
        -webkit-box-orient:vertical;
    }

</style>
