<template>
    <div class="main">
        <el-page-header @back="goBack" content="" style="margin-bottom: 20px">
        </el-page-header>
        <el-form label-width="100px" class="demo-ruleForm" >
            <el-form-item label="" prop="name">
                <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>
            <el-form-item label="" >
                <el-col :span="11">
                    <el-form-item prop="date1">
                        <el-date-picker type="date" placeholder="" v-model="ruleForm.startTime" style="width: 100%;"></el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col class="line" :span="2">-</el-col>
                <el-col :span="11">
                    <el-form-item prop="date2">
                        <el-date-picker type="date" placeholder="" v-model="ruleForm.endTime" style="width: 100%;"></el-date-picker>
                    </el-form-item>
                </el-col>
            </el-form-item>
            <el-row :gutter="20">
                <el-col :span="7">
            <el-form-item label="id" prop="id">
                <el-input v-model="ruleForm.id" :disabled="true"></el-input>
            </el-form-item>
                </el-col>
                <el-col :span="7">
            <el-form-item label="" prop="showRoom">
                <el-input v-model="ruleForm.showRoom"></el-input>
            </el-form-item></el-col>
                <el-col :span="7">
                    <el-form-item label="" prop="acreage">
                        <el-input v-model="ruleForm.acreage"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="20">
                <el-col :span="7">
                    <el-form-item label="" prop="acreage">
                    <el-select   v-model="ruleForm.trade" placeholder="">
                        <el-option v-for="item in options" :key="item.value" :label="item.label"
                                   :value="item.value"></el-option>
                    </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="7">
                    <el-form-item label="" prop="organizer">
                        <el-input v-model="ruleForm.organizer"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="7">
                    <el-form-item label="id" prop="contractorId">
                        <el-input v-model="ruleForm.contractorId"></el-input>
                    </el-form-item></el-col>
            </el-row>
            <el-row :gutter="20">
                <el-col :span="7">
                    <el-form-item label="" prop="session">
                        <el-input v-model="ruleForm.session"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="7">
                    <el-form-item label="" prop="period">
                        <el-input v-model="ruleForm.period"></el-input>
                    </el-form-item></el-col>
                <el-col :span="7">
                    <el-form-item label="" prop="Tel">
                        <el-input v-model="ruleForm.Tel"></el-input>
                    </el-form-item></el-col>
            </el-row>
            <el-row :gutter="20">
                <el-col :span="7">
                    <el-form-item label="" prop="status">
                        <el-select   v-model="ruleForm.status" placeholder="">
                            <el-option v-for="item in options2" :key="item.value" :label="item.label"
                                       :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="11">
                    <el-form-item label="URL" prop="picture">
                        <el-input v-model="ruleForm.picture"></el-input>
                    </el-form-item></el-col>
            </el-row>
            <el-row :gutter="20">
                <el-col :span="14">
                    <el-form-item label="" prop="introduction">
                        <el-input v-model="ruleForm.introduction" type="textarea" :autosize="{ minRows: 3, maxRows: 4}"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item>
                <el-button type="primary" @click="submit()">{{buttonName}}</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "edit",
        data() {
            return {
                ruleForm: {
                    name: '',
                    id:"",
                    startTime:"",
                    endTime:"",
                    exhibitionHallId:"",
                    showRoom:"",
                    acreage:"",
                    trade:"",
                    organizer:'',
                    contractorId:'',
                    session:'',
                    period:"",
                    introduction:'',
                    Tel:'',
                    status:"",
                    picture:''
                },
                rule:{
                    id:[]
                },
                buttonName:'',
                options:[
                    {
                        value:"1",
                        label:''
                    },
                    {
                        value:"2",
                        label:''
                    },
                    {
                        value:"3",
                        label:''
                    },
                    {
                        value:"4",
                        label:''
                    },
                    ],
                options2:[
                    {
                        value:0,
                        label:''
                    },
                    {
                        value:1,
                        label:''
                    },
                    {
                        value:2,
                        label:''
                    },
                    {
                        value:3,
                        label:''
                    },
                    {
                        value:4,
                        label:''
                    },
                    {
                        value:5,
                        label:''
                    },
                    {
                        value:6,
                        label:''
                    },
                    {
                        value:7,
                        label:''
                    }
                    ]
            };
        },
        methods: {
            submit() {
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'success'
                }).then(() => {
                    this.axios.put('/api/exhibition/Admin/updateExhibitionInfo',this.ruleForm).then(()=>{
                        this.$message({
                            message: '',
                            type: 'success'
                        });
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            goBack(){
                this.$router.back()
            }
        },
        created(){
            if(this.$route.params.data){
                this.ruleForm=this.$route.params.data
                this.ruleForm.startTime=new Date(this.ruleForm.startTime)
                this.buttonName=''
            }

        }
    }
</script>

